@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yaldevi&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
}
body {
  height: 1000px;
}
.menu {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin-top: 40px;
  font-family: "Rubik", sans-serif;
}
.title {
  color: #0bbd40;
  font-size: 45px;
  padding-right: 35%;
  text-shadow: 12px 2px 10px #13c749ad;
}
.menu nav,
.title {
  width: 50%;
}
.menu ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px 15% 0 0;
}
li {
  list-style: none;
  height: 45px;
  text-align: center;
  padding-right: 4%;
  cursor: pointer;
}
.link {
  text-decoration: none;
  color: #838383;
  font-size: 27px;
  font-family: "Bebas Neue", cursive;
}
.link:hover {
  text-decoration: none;
  color: #3c3f3b;
  font-size: 29px;
  padding-bottom: 5px;
  border-bottom: solid 2px #0bbd40;
}

.find{
  margin-top: 60px;
  margin-bottom: 20px;
}
.search{
  width: 30%;
}
table {
  width: 80%;
  margin: 0 10%;
}
th {
  padding: 10px 8px 30px;
  color: #0bbd40;
  font-size: 35px;
  text-shadow: 10px 1px 8px #1ead4981;
}
td {
  color: #3c3f3b;
  font-size: 29px;
  padding-bottom: 25px;
  border-bottom: solid 1px #0bbd40;
}
.search_but{
  margin: 15px 45%;
  width: 10%;
  background-color: #3c3f3b;
}
.search_but:hover{
  background-color: #838383;
  color: #3c3f3b; 
}







